import React, { ActivityIndicator, View, 
  Text,
   StyleSheet
  ,ToastAndroid
  ,Image } from "react-native";

const colors = [
 
 
 
  "darkblue",
  "burlywood",
  "brown",
  "cornflowerblue",
  "teal",
  "darkcyan",
];
export function RandomColor() {
  const randomIndex = Math.floor(Math.random() * colors.length);
  const item = colors[randomIndex];
  return item;
}

export const Hr=(text)=>{
    return(
    <View style={{flexDirection: 'row',marginVertical:10}}>
    <View style={{backgroundColor: global.MasterColor, height: 2, flex: 1, alignSelf: 'center'}} />
    <Text style={{ alignSelf:'center', paddingHorizontal:5, fontSize: 16,color: global.MasterColor }}>{text}</Text>
    <View style={{backgroundColor: global.MasterColor, height: 2, flex: 1, alignSelf: 'center'}} />
  </View>);
  }
  
export const ShowToast = (message) => {
    ToastAndroid.show(message, ToastAndroid.SHORT);
};
export function LoadingAnimation() {
  return (
    <View style={styles.indicatorWrapper}>
      <ActivityIndicator size="large" style={styles.indicator} />
      <Text style={styles.indicatorText}>جاري التحميل...</Text>
    </View>
  );
}

export function renderLoading() {
  return (  <View style={styles.indicatorWrapper}>
    <Image
          style={styles.image}
          source={require("../assets/Images/cube.gif")}
        />
    <Text style={styles.indicatorText}>جاري التحميل...</Text>
  </View>)
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  image:{
    width:100,
    height:100
  },
  indicatorWrapper: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
  },
  indicator: {},
  indicatorText: {
    fontSize: 16,
    color:global.MasterColor,
    marginTop: 12,
  },
});
