import React from "react";
import {
  View,
  Text,
  StyleSheet,
  TouchableOpacity,
  TextInput,
  StatusBar,
} from "react-native";
import { useState } from "react";
// import Checkbox from 'expo-checkbox';
import Icon from "react-native-vector-icons/FontAwesome";
import Icons from "react-native-vector-icons/Feather";
import * as Animatable from "react-native-animatable";
import {  MAIN_URL } from "../Lib/Constant";
import { ShowToast, LoadingAnimation } from "../Lib/Components";


export default function LoginScreen({navigation}) {
  const [data, setData] = useState({
    userName: "",
    password: "",
    checkInputText: false,
    passwordVisibilty: false,
    isLoading: false,
  

  });


  const setUserNameValue = (val) => {
    if (val.length != 0) {
      setData({
        ...data,
        userName: val,
        checkInputText: true,
      });
    } else {
      setData({
        ...data,
        userName: val,
        checkInputText: false,
      });
    }
  };
  const setPasswordValue = (val) => {
    setData({
      ...data,
      password: val,
    });
  };
  const changePasswordVisibility = () => {
    setData({
      ...data,
      passwordVisibilty: !data.passwordVisibilty,
    });
  };

  const changeIsLoadingStatus = (newStatus) => {
    setData({
      ...data,
      isLoading: newStatus,
    });
  };
  //---------------------------------------------------------------------
  saveCurrentSchool =  (id,schoolName,fullName) => {
    try {

        global.SchoolID =id;
        global.SchoolName =schoolName.replace(/\"/g, "");
        global.UserName =fullName.replace(/\"/g, "");
       // global.IsLogged=true;
     
  
    } catch (err) {
      alert(err)
    }
  }
  //-----------------------------------------------------------------------
  async function Login() {
    if (data.userName=="" || data.password==""){
      ShowToast("يجب إدخال كود المستخدم وكلمة المرور");
      return 0;
    }
    changeIsLoadingStatus(true);
    const url = await fetch(
      MAIN_URL + `School?userName=${data.userName}&password=${data.password}`
    );
    const jsonResult = await url.json();
    changeIsLoadingStatus(false);
    if (jsonResult.schoolID) {
        
        saveCurrentSchool(JSON.stringify(jsonResult.schoolID)
        ,JSON.stringify(jsonResult.schoolName),
        JSON.stringify(jsonResult.fullName)
       );

        navigation.replace("StudentScreen");
    } else {
      ShowToast(jsonResult.errorMessage);
    }
  }
  
 
  return (
    <View style={styles.container}>
     <StatusBar backgroundColor={global.MasterColor} barStyle="light-content" />
      <View style={styles.header}>
      <Text style={styles.headerText}>W<Text style={{fontSize:14}}>elcome to </Text></Text>
        <Text style={styles.headerText}>Cube Social {global.Rtl}</Text>
      </View>
      <Animatable.View
        style={styles.footer}
        animation="zoomInUp"
        duration={3000}
      >
     
        <View style={[styles.action,{ flexDirection: global.Rtl ? "row-reverse" : "row" }]}>
          <Icon name="user-o" size={30} color={global.MasterColor}/>
          <TextInput
         placeholder="كود المستخدم"
            autoCapitalize="none"
            onChangeText={setUserNameValue}
            style={styles.textInput}
          />
          {data.checkInputText ? <Icons name="check" size={30} color={global.MasterColor} /> 
          : null}
        </View>
   
        <View style={[styles.action,{ flexDirection: global.Rtl ? "row-reverse" : "row" }]}>
          <Icon name="key" size={30} color={global.MasterColor}/>
          <TextInput
            placeholder="كلمة المرور"
  
            secureTextEntry={data.passwordVisibilty ? false : true}
            onChangeText={(val) => setPasswordValue(val)}
            style={styles.textInput}
          />
          <TouchableOpacity onPress={() => changePasswordVisibility()}>
            {data.passwordVisibilty ? (
              <Icon name="eye" size={20} color={global.MasterColor} />
            ) : (
              <Icon name="eye-slash" size={20} color="gray" />
            )}
          </TouchableOpacity>
         
        </View>

        <View style={styles.button}>
          {data.isLoading ? (
            LoadingAnimation()
          ) : (
            <View>
             
          
            <TouchableOpacity onPress={() => Login()}>
              <View
               
                style={styles.signIn}
              >
                <Text style={[styles.textSign, { color: "#fff" }]}>
                 
                  دخــــــول
                </Text>
              </View>
            </TouchableOpacity>
            </View>
          )}

         
        </View>
      </Animatable.View>
      <View style={styles.ver}><Text style={{color:"#fff"}}>Ver 1.0.3</Text></View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: global.MasterColor,

  },
 
  paragraph: {
    fontSize: global.height *0.09,
    marginLeft:10,

  },
  header: {
    flex: 2,
    justifyContent: "flex-end",
    paddingHorizontal: 20,
    paddingBottom: 50,
  },
  ver: {
    flex: 0.5,
    justifyContent: "center",
    alignItems:"center",
    
    // paddingHorizontal: 20,
    // paddingBottom: 50,
  },
  button: {
    marginTop: 30,
    flex: 1,
  },
  headerText: {
    color: "#fff",
    fontSize: 30,
  },
  
  buttonTitle: {
    color: "#fff",
    fontSize: 14,
  },
  textInput: {
    flex: 1,
    paddingStart: 10,
    marginTop: 20 ,
    fontSize: global.height*0.09,
    borderBottomWidth:1,
    borderBottomColor:global.MasterColor,
    height:40,
   textAlign:"right"

  },
  action: {
    //flexDirection: "row",
    marginVertical:20,
    
    
  },
  footer: {
    flex: 5,
    backgroundColor: "#fff",
    borderRadius:30,
    paddingVertical: 50,
    paddingHorizontal: 30,
    marginHorizontal:20
  },
  textSign: {
    fontSize: 15,
  color:'#fff',
    fontWeight: "bold",
  },
  signIn: {
    width: "100%",
    backgroundColor:global.MasterColor,
    height: 50,
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 10,
  },
});
