import React from "react";
import {
  View,
  Text,
  StyleSheet,
  TouchableOpacity,
  TextInput,
  Image,
  StatusBar,
  FlatList,
  Pressable,
} from "react-native";
import * as Animatable from "react-native-animatable";
import { LoadingAnimation } from "../Lib/Components";
import { MAIN_URL } from "../Lib/Constant";
import { useState, useEffect } from "react";
import filter from "lodash.filter";

export default function StudentScreen({ navigation }) {
  const [data, setData] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [isLoding, setIsLoading] = useState(false);
  const [refreshing, setrefreshing] = useState(false);
  const [error, setError] = useState(null);
  const [fullData, setFullData] = useState([]);
  const [response, setResponse] = useState(null);

  useEffect(() => {
    setIsLoading(true);

    loadData();
  }, []);

  async function loadData() {
    try {
      const reponse = await fetch(
        MAIN_URL + `Chat/StudentsForChat?School=${global.SchoolID}`
      );
      const json = await reponse.json();
      if (json.errorMessage) {
        setResponse(json.errorMessage);
      } else {
        setData(json);
        setFullData(json);
        setIsLoading(false);
        setrefreshing(false);
      }
    } catch (error) {
      setError(error);
      setIsLoading(false);
      console.log(error);
    }
  }
  //-------------------------------------------
  handleRefresh = () => {
    setrefreshing(true);
    loadData();
  };
  //-------------------------------------------
  handleQuery = (query) => {
    setSearchQuery(query);
    const filteredData = filter(fullData, (item) => {
      return containes(item, query);
    });
    setData(filteredData);
  };
  //-----------------------------------------------
  const containes = ({ studentName, levelName }, query) => {
    if (studentName.includes(query) || levelName.includes(query)) return true;

    return false;
  };
  //---------------------------------------------
  setMessageAsRead = async (studentID) => {
    try {
      const reponse = await fetch(
        MAIN_URL + `Chat/SetStudentMessageAsRead?Student=${studentID}`
      );
    } catch (error) {
      console.error(error);
    }
  };
  //--------------------------------------------
  const clickEventListener = (studentID, studentName,unreadMessages ) => {

    if (unreadMessages>0)
            setMessageAsRead(studentID);
    navigation.navigate("Chat", {
      studentID: studentID,
      studentName: studentName,
    });
  };
  //-------------------------------------------
  const renderSearchPart = () => {
    return (
      <View style={styles.parent}>
        <TextInput
          style={styles.searchBox}
          clearButtonMode="always"
          placeholder="بحث ..."
          value={searchQuery}
          onChangeText={(query) => handleQuery(query)}
        />
        <TouchableOpacity
          style={styles.closeButtonParent}
          onPress={() => {
            setSearchQuery("");
            setData(fullData);
          }}
        >
          <Image
            style={styles.closeButton}
            source={require("../assets/Images/close.png")}
          />
        </TouchableOpacity>
      </View>
    );
  };
  renderListItem = ({ item }) => {
    return (
      <Pressable
        onPress={() => {
          clickEventListener(item.studentID, item.studentName,item.unreadMessages );
        }}
      >
        <View
          style={[
            styles.itemContainer,
            { flexDirection: global.Rtl ? "row-reverse" : "row" },
          ]}
        >
          <Image
            source={require("../assets/Images/student.png")}
            style={styles.image}
          />
          <View style={{ width: "77%" }}>
            <View style={{ flexDirection: global.Rtl ? "row-reverse" : "row" }}>
              <Text style={styles.dataText}>{item.studentName}</Text>
              {item.unreadMessages > 0 ? (
                <View style={styles.Badge}>
                  <Text style={styles.BadgeText}>{item.unreadMessages}</Text>
                </View>
              ) : null}
            </View>
            <Text style={styles.detailText}>
              {item.levelName}({item.className})
              <Text style={styles.phoneText}>({item.phoneNo})</Text>
            </Text>
          </View>
        </View>
      </Pressable>
    );
  };
  return (
    <View style={styles.container}>
      <StatusBar
        backgroundColor={global.MasterColor}
        barStyle="light-content"
      />
      <View style={styles.header}>
      <View style={{ flexDirection: global.Rtl ? "row-reverse" : "row" }}  >
        <Text style={styles.recordsCount}>
          عدد الرسائل الغير مقرؤة
         
          </Text>
          <View style={[styles.Badge,{backgroundColor: "pink"}]}>
            <Text style={styles.BadgeText}>
              {Object.values(data).reduce(
                (r, { unreadMessages }) => r + unreadMessages,
                0
              )}
            </Text>
          </View>
        
      </View>
      </View>
      <Animatable.View
        style={styles.footer}
        animation="flipInY"
        duration={2000}
      >
        {error ? (
          <Text> لا يمكن جلب البيانات .. تأكد من إتصالك بالانترنت</Text>
        ) : response ? (
          <Text> {response}</Text>
        ) : isLoding ? (
          LoadingAnimation()
        ) : (
          <View>
            {renderSearchPart()}

            <FlatList
              data={data}
              onRefresh={handleRefresh}
              refreshing={refreshing}
              renderItem={renderListItem}
              style={{ marginVertical: 10 }}
              contentContainerStyle={{ paddingBottom: 30 }}
              keyExtractor={(item, index) => {
                return item.studentID;
              }}
            />
          </View>
        )}
      </Animatable.View>
    </View>
  );
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: global.MasterColor,
  },

  header: {
    flex: 1,
    justifyContent: "flex-end",
    paddingHorizontal: 20,
    paddingBottom: 20,
  },
  phoneText: {
    color: global.MasterColor,
    fontSize: global.height * 0.07,
  },

  footer: {
    flex: 9,
    backgroundColor: "#fff",
    borderTopLeftRadius: 30,
    borderTopRightRadius: 30,
    paddingVertical: 30,
    paddingHorizontal: 20,
  },

  dataText: {
    fontSize: global.height * 0.1,
    fontWeight: "bold",
    borderRadius: 10,
    flex: 2,
  },
  detailText: {
    fontSize: global.height * 0.08,
    fontWeight: "bold",
    color: "gray",
    borderRadius: 10,
    flex: 2,
  },
  recordsCount: {
    fontSize: global.height * 0.1,
    color: "#fff",
    paddingRight: "1%",
    fontWeight: "bold",
  },
  dataSection: { flex: 1, flexDirection: "row", paddingVertical: 3 },
  parent: {
    marginHorizontal: 25,
    borderBottomWidth: 1,
    borderColor: global.MasterColor,
    flexDirection: "row",
    justifyContent: "space-between",
  },
  closeButtonParent: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
  },
  searchBox: {
    height: 40,
    paddingHorizontal: 10,

    width: "90%",

    borderRadius: 7,
  },
  closeButton: {
    height: 16,
    width: 16,
  },
  closeButtonParent: {
    justifyContent: "center",
    alignItems: "center",
    marginRight: 5,
  },

  itemContainer: {
    borderRadius: 20,
    backgroundColor: global.LightColor,

    paddingStart: 10,
    paddingVertical: 10,
    marginVertical: 5,
  },
  image: {
    width: global.height * 0.3,
    height: global.height * 0.3,
    marginRight: global.height * 0.09,
  },
  Badge: {
    width: global.height * 0.25,
    height: global.height * 0.25,
    justifyContent: "center",
    alignItems: "center",

    top: -5,
    right: -10,
    backgroundColor: "#EC7063",
    borderRadius: 50,
  },
  
  BadgeText: {
    color: "#fff",
    fontSize: 14,
    fontWeight: "600",
  },
});
