import React, { useState, useCallback, useEffect, useRef } from "react";
import {
  View,
  StyleSheet,
  StatusBar,
  Pressable,
  RefreshControl,
} from "react-native";
import { GiftedChat, Send, Bubble, Time } from "react-native-gifted-chat";
import MaterialCommunityIcons from "react-native-vector-icons/MaterialCommunityIcons";
import { renderLoading } from "../Lib/Components";

import { MAIN_URL } from "../Lib/Constant";



export function Chat({ navigation, route }) {
  const [messages, setMessages] = useState([]);
  const [refreshing, setrefreshing] = useState(false);
  const [isLoding, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const chatRef = useRef(null);
  useEffect(() => {
    navigation.setOptions({
      title: "محادثة" + " /" + route.params.studentName,
    });
    setIsLoading(true);
    loadData();
  }, []);

  const onSend = useCallback((messages = []) => {
    sendMessage(messages);
    setMessages((previousMessages) =>
      GiftedChat.append(messages, previousMessages)
    );
  }, []);
  async function loadData() {
    try {
      const reponse = await fetch(
        MAIN_URL + `Chat?student=${route.params.studentID}`
        );
      const json = await reponse.json();
      setIsLoading(false);
      if (json.length > 0) setMessages(json);

      setrefreshing(false);
    } catch (error) {
      setError(error);
      setIsLoading(false);
      console.log(error);
    }
  }
  const renderBuble = (props) => {
    return (
      <Bubble
        {...props}
        wrapperStyle={{
          right: {
            backgroundColor: global.LightColor,
            // color:'#fff'
          },
          right: {
            backgroundColor: global.LightColor,
            // color:'#fff'
          },
          left: {
            // backgroundColor:'#EBF5FB',
            color: "#2471A3",
          },
        }}
        textStyle={{
          right: { color: "#000" },
        }}
      />
    );
  };
  //-----------------------------------
  renderTime = (props) => {
    return (
      <Time
        {...props}
        containerStyle={{ left: { alignSelf: "flex-end", flex: 1 } }}
        timeTextStyle={{
          left: { color: "#3c3c434d", fontSize: 9, textAlign: "right" },
          right: { color: "#3c3c434d", fontSize: 9 },
        }}
      />
    );
  };
 
  //----------------------------------
  const renderSend = (props) => {
    return (
      <Send {...props}>
        <View>
          <MaterialCommunityIcons
            name="send-circle"
            size={44}
            color={global.MasterColor}
          />
        </View>
      </Send>
    );
  };

  handleRefresh = () => {
    setrefreshing(true);
    loadData();
    if (chatRef?.current) {
      chatRef.current.scrollToEnd(true);
    }
  };

  sendMessage = async (messages) => {
        try {
          const response = await fetch(
            MAIN_URL +`Chat/SaveChatMessage`,
            {
              method: "POST",
              headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
              },
              body: JSON.stringify({
                _id: messages[0].user._id,
                studentID: route.params.studentID,
                text: messages[0].text,
                createdAt: messages[0].createdAt,
                isRead: false,
              }),
            }
          );
          //  const json = await response.json();
        //  setIsLoading(true);
          //  console.log(json)
        } catch (error) {
          console.error(error);
        } 
      };
    

  return (
    <View style={styles.container}>
      <StatusBar
        backgroundColor={global.MasterColor}
        barStyle="light-content"
      />
      <View style={styles.header}></View>
      <View style={styles.footer}>
        <View>
          <Pressable onPress={() => handleRefresh()}>
            <MaterialCommunityIcons
              name="refresh"
              size={44}
              color={global.MasterColor}
            />
          </Pressable>
        </View>
        {isLoding ? (
          renderLoading()
        ) : (
          <GiftedChat
            messages={messages}
            inverted={false}
            placeholder="اكتب هنا .."
            messageContainerRef={chatRef}

            scrollToBottom
            listViewProps={{
              refreshControl: (
                <RefreshControl
                  refreshing={refreshing}
                  onRefresh={() => handleRefresh()}
                />
              ),
            }}
            alwaysShowSend
            renderSend={renderSend}
            renderBubble={renderBuble}
            renderTime={renderTime}
            onSend={(messages) => onSend(messages)}
            user={{
              _id: 0,
            }}
          />
        )}
      </View>
    </View>
  );
}
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: global.MasterColor,
  },
  header: {
    flex: 0.2,
    justifyContent: "flex-end",
  },
  indicatorWrapper: {
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
  },

  footer: {
    flex: 10,
    backgroundColor: "#fff",
    borderRadius: 30,
    paddingVertical: 25,
    paddingHorizontal: 10,
    marginHorizontal: 10,
    marginBottom: 10,
  },
});
